.header {

    &__title {
        margin: 0; }

    &__content {
        width: 100%;
        height: 100vh;
        min-height: 400px;
        position: relative;
        display: flex;
        flex-direction: column;
        background-image: url('../img/header.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;

        &::after {
            content: '';
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            background-color: rgba(0,0,0,.7);
            top: 0;
            left: 0;
            z-index: 1; } }

    &__brand {
        position: relative;
        z-index: $z_header;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        padding-left: 60px;
        color: white; }

    &__brand-title {
        max-width: 980px;
        font-size: 80px;
        line-height: .95; } }
