h1,h2,h3,h4,h5,h6 {
    margin-top: 0; }

.test-img {
    height: 250px;
    background-image: url('../img/header.jpg');
    background-size: cover;
    background-position: center;
    outline: 1px solid grey; }

html {
    box-sizing: border-box; }

*,
*::before,
*::after {
    box-sizing: inherit; }

img {
    max-width: 100%; }

.ul-reset {
    list-style: none;
    padding: 0; }

.body {
    background-color: $bg-color;
    color: $dark-text-color;
    font-family: $font;

    &.fix {
        overflow: hidden; } }

.link {
    color: inherit;
    text-decoration: none;

    &-accent {
        color: $accent-color; }

    &-usual {
        color: $usual-color; } }

.btn {

    &-reset {
        border: none;
        background: transparent;
        cursor: pointer; }

    &-accent {
        color: $accent-color; } }

.site-container {
    overflow: hidden; }

.site-wrapper {
    padding-left: $sidenav-width; }

@import "./_media";
