.container {
  --container-max-width: 1200px;
  --container-padding: 15px;
  --gutter: 30px;
  width: 100%;
  max-width: var(--container-max-width);
  margin: 0 auto;
  padding: 0 var(--container-padding);
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc(var(--container-padding) * -1);
  margin-right: calc(var(--container-padding) * -1);
}

.col {
  padding: 0 calc(var(--gutter) / 2);
}

.col .-cell {
  margin-bottom: var(--container-padding);
}

.-cols-1 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 1));
}

.-cols-2 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 2));
}

.-cols-3 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 3));
}

.-cols-4 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 4));
}

.-cols-5 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 5));
}

.-cols-6 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 6));
}

.-cols-7 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 7));
}

.-cols-8 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 8));
}

.-cols-9 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 9));
}

.-cols-10 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 10));
}

.-cols-11 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 11));
}

.-cols-12 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 12));
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Black.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Thin.woff2") format("woff2");
  font-weight: 100;
  font-display: swap;
  font-style: normal;
}

.nav-menu {
  position: relative;
  z-index: 5;
}

.nav-menu__sidenav {
  position: fixed;
  top: 0;
  left: 0;
  width: 60px;
  height: 100vh;
  background-color: #222;
  z-index: 6;
}

.nav-menu__content {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  padding: 60px 0 60px 60px;
  background-color: #272a2b;
  transform: translateX(-100%);
  transition: transform 0.5s ease-in;
  overflow: auto;
}

.nav-menu__meta {
  background-color: #2e3132;
}

.nav-menu.open-menu .nav-menu__content {
  transform: translateX(0);
}

.nav-menu.open-menu .nav-trigger__text {
  display: none;
}

.nav-menu.open-menu .nav-trigger__text-close {
  display: inline;
}

.nav-trigger {
  background: transparent;
  border: none;
  outline: none;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  cursor: pointer;
  min-height: 400px;
}

.nav-trigger__text-close {
  display: none;
}

.header__title {
  margin: 0;
}

.header__content {
  width: 100%;
  height: 100vh;
  min-height: 400px;
  position: relative;
  display: flex;
  flex-direction: column;
  background-image: url("../img/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.header__content::after {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  top: 0;
  left: 0;
  z-index: 1;
}

.header__brand {
  position: relative;
  z-index: 2;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding-left: 60px;
  color: white;
}

.header__brand-title {
  max-width: 980px;
  font-size: 80px;
  line-height: .95;
}

.section {
  padding-top: 45px;
  padding-bottom: 55px;
}

.section__services {
  background-color: #3E4449;
  color: white;
}

.services__content {
  display: flex;
}

.services__desc {
  flex: 0 0 70%;
}

.services__list {
  flex: 0 0 30%;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
}

.test-img {
  height: 250px;
  background-image: url("../img/header.jpg");
  background-size: cover;
  background-position: center;
  outline: 1px solid grey;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

.ul-reset {
  list-style: none;
  padding: 0;
}

.body {
  background-color: #fff;
  color: #323738;
  font-family: "Roboto";
}

.body.fix {
  overflow: hidden;
}

.link {
  color: inherit;
  text-decoration: none;
}

.link-accent {
  color: #1ca8c7;
}

.link-usual {
  color: #5f7777;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.btn-accent {
  color: #1ca8c7;
}

.site-container {
  overflow: hidden;
}

.site-wrapper {
  padding-left: 60px;
}

@media (max-width: 992px) {
  .site-wrapper {
    padding-left: 0;
    padding-top: 60px;
  }
  .nav-menu__sidenav {
    width: 100%;
    height: 60px;
  }
  .nav-trigger {
    min-height: auto;
  }
  .header__brand {
    align-items: center;
    padding: 0;
  }
  .header__brand-title {
    width: auto;
    text-align: left;
  }
}
