.container {
    --container-max-width: 1200px;
    --container-padding: 15px;
    --gutter: 30px;
    width: 100%;
    max-width: var(--container-max-width);
    margin: 0 auto;
    padding: 0 var(--container-padding);
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin-left: calc(var(--container-padding) * -1);
    margin-right: calc(var(--container-padding) * -1);
}

.col {
    padding: 0 calc(var(--gutter) / 2);
}

.col .-cell {
    margin-bottom: var(--container-padding);
}

.-cols-1 .col { flex: 0 0 calc((100% / 12) * calc(12 / 1)); }
.-cols-2 .col { flex: 0 0 calc((100% / 12) * calc(12 / 2)); }
.-cols-3 .col { flex: 0 0 calc((100% / 12) * calc(12 / 3)); }
.-cols-4 .col { flex: 0 0 calc((100% / 12) * calc(12 / 4)); }
.-cols-5 .col { flex: 0 0 calc((100% / 12) * calc(12 / 5)); }
.-cols-6 .col { flex: 0 0 calc((100% / 12) * calc(12 / 6)); }
.-cols-7 .col { flex: 0 0 calc((100% / 12) * calc(12 / 7)); }
.-cols-8 .col { flex: 0 0 calc((100% / 12) * calc(12 / 8)); }
.-cols-9 .col { flex: 0 0 calc((100% / 12) * calc(12 / 9)); }
.-cols-10 .col { flex: 0 0 calc((100% / 12) * calc(12 / 10)); }
.-cols-11 .col { flex: 0 0 calc((100% / 12) * calc(12 / 11)); }
.-cols-12 .col { flex: 0 0 calc((100% / 12) * calc(12 / 12)); }
